/**
 * Super basic stylings
 *
 */
* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

html {
  height: 100%;
}

html, body {
  min-height: 100%;
}

body {
  position: relative;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

strong {
  font-weight: bold;
}

img {
  -ms-interpolation-mode: bicubic;
}

.column img, .columns img {
  max-width: 100%;
}

img {
  display: block;
  max-width: 100%;
  width: 100%;
}

p em {
  font-style: italic;
}

section, article, aside, header, footer, nav, figure, figcaption {
  display: block;
}

time, mark {
  display: inline-block;
}

/**
 * All helpers use the !important keyword, if ever necessary their
 * output might be overriden with the !important keyword in a
 * property located lower in the cascade.
 *
 */
/**
 *	Helpers Index:
 *		Position
 *		Display
 *		Gutters
 *		Paddings and Margins
 *		JS Classes
 *
 */
/*************************** Position ***************************/
.absolute {
  position: absolute !important;
}

.relative {
  position: relative !important;
}

.fixed {
  position: fixed !important;
  max-width: 100% !important;
  width: 100% !important;
  left: 0;
}

.leer-mas__container {
  position: relative;
  overflow: hidden;
}

.flip {
  -moz-transform: rotate(180deg) !important;
  -o-transform: rotate(180deg) !important;
  -webkit-transform: rotate(180deg) !important;
  transform: rotate(180deg) !important;
}

.centerXY {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.left {
  float: left !important;
}

.right {
  float: right !important;
}

.top56 {
  top: 56px;
}

.ontop {
  z-index: 99 !important;
}

.z-0 {
  z-index: 0 !important;
}

/*************************** Layout ***************************/
.clearfix:before, .clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  *zoom: 1;
}

/*************************** Display ***************************/
.d-bl {
  display: block !important;
}

.inline {
  display: inline !important;
}

.inline-block {
  display: inline-block !important;
  float: left;
  vertical-align: top;
}

/*************************** Gutters ***************************/
/*************************** Paddings and Margins ***************************/
.p0 {
  padding: 0 !important;
}

.pr0 {
  padding-right: 0 !important;
}

.pl0 {
  padding-left: 0 !important;
}

.m0 {
  margin: 0 !important;
}

.mb0 {
  margin-bottom: 0 !important;
}

.m50 {
  margin-bottom: 50px !important;
}

/*************************** Arrows ***************************/
.icon-arrow-up:after {
  content: '\e801';
}

.icon-arrow-down:after {
  content: '\e800';
}

/*************************** Drags, Selects ***************************/
.undraggable-unselectable-cascading *,
.undraggable-unselectable {
  user-drag: none !important;
  -webkit-user-drag: none !important;
  user-select: none !important;
  -moz-user-select: none !important;
  -webkit-user-select: none !important;
  -ms-user-select: none !important;
}

.unselectable {
  user-select: none !important;
  -moz-user-select: none !important;
  -webkit-user-select: none !important;
  -ms-user-select: none !important;
}

/*************************** Animations ***************************/
.transition-fast {
  transition: all 0.3s;
}

.transition-slow {
  transition: all 0.7s;
}

.hover-scale-up:hover {
  transform: scale(1.2) !important;
}

/*************************** JS Classes ***************************/
.noScroll_JS {
  overflow: hidden !important;
}

.wrap {
  width: 100%;
  margin: 0 auto;
