/*Input Elements' Params*/
input, textarea {
  background-color: #FFFFFF;
  color: #000000;
  border: 1px solid #000000;
  border-radius: initial;
  padding: 0 .5em;
  -moz-appereance: none;
  -webkit-appearance: none;
}

input, textarea {
  width: 100%;
}

