  resize: vertical;
}

input[type="submit"],
input[type="button"] {
  background-color: #666666;
  display: block;
}

input[type="date"],
input[type="time"],
input[type="button"] {
  width: auto;
}

input::-webkit-datetime-edit {
  color: black;
}

input::-webkit-inner-spin-button,
input::-webkit-calendar-picker-indicator {
  cursor: pointer;
  opacity: 1;
}

input[type="checkbox"],
input[type="radio"] {
  cursor: pointer;
  padding: 0;
  height: 1rem;
  width: 1rem;
  vertical-align: text-top;
}

input[type="checkbox"]:checked,
input[type="radio"]:checked {
  background-color: black;
}

input[type="radio"] {
  border-radius: 100%;
}

select {
  -moz-appereance: none;
  -webkit-appearance: none;
  cursor: pointer;
}

input:focus, select:focus, textarea:focus, button:focus {
  outline: none;
}

::-webkit-input-placeholder {
