///
/// Private mixin that lets all css propierties animate thruogh transitions
/// 
/// 
/// @author Daniel Cbs
/// @since 1.1.13
///

@mixin ease-transition($speed: 0.3s) {
   -webkit-transition: all $speed ease;
   -moz-transition: all $speed ease;
   -o-transition: all $speed ease;
   transition: all $speed ease;
}
